body {
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.tab {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.tab:hover {
  background-color: #0056b3;
}

.food-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.food-card {
  position: relative;
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: calc(33.333% - 20px);
  box-sizing: border-box;
}

.food-card img {
  max-width: 100%;
  border-radius: 10px;
}

.food-card h3 {
  margin-top: 10px;
}

.food-card .price {
  color: #007bff;
  font-weight: bold;
}

.food-card .ingredients {
  margin: 10px 0;
}

.food-card .cart-indicator {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #dc3545;
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.cart-buttons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.cart-buttons button {
  padding: 5px 10px;
  background-color: #28a745;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.cart-buttons button.minus {
  background-color: #dc3545;
}

.cart-buttons button.minus.hidden {
  display: none;
}

.cart-buttons button:hover {
  opacity: 0.8;
}

.cart {
  margin-top: 20px;
  text-align: right;
}
